.hero{
    height: 100vh;
    display: flex;
    background: linear-gradient(rgb(139, 83, 188),rgb(224, 120, 68));
}
.hero-left{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    padding-left:180px ;
    line-height: 1.1;
}
.hero-left h2{
    color: brown;
    font-size: 26px;
    font-weight: 600;
}
.hero-left p{
    color: rgb(12, 10, 10);
    font-size: 80px;
    font-weight: 700;
}
.hero-hand-icon{
    display: flex;
    align-items: center;
    gap: 20px;


}
.hero-hand-icon img{
    width: 105px;
}
.hero-latest-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 20px;
    background: rgb(55, 11, 187);
    color: white;
    font-size: 22px;
    font-weight: 500;
}

.hero-right{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media (max-width:1280px) {
    .hero-left{
        padding-left: 100px;
    }
    .hero-left h2{
        font-size: 22px;
    }
    .hero-left p{
        font-size: 60px;
    }
    .hero-hand-icon img{
        width: 70px;
    }
    .hero-latest-btn{
        gap: 10px;
        width: 250px;
        height: 50px;
        font-size: 18px;
        margin-top: 10px;
    }
    .hero-right img{
        width: 500px;
         
    }
    
    
}
@media (max-width:1024px) {
    .hero-left{
        padding-left: 80px;
    }
    .hero-left h2{
        font-size: 19px;
    }
    .hero-left p{
        font-size: 50px;
    }
    .hero-hand-icon img{
        width: 60px;
    }
    .hero-latest-btn{
       
        width: 200px;
        height: 40px;
        font-size: 16px;
        
    }
    .hero-right img{
        width:450px;
         
    }
    
}
@media (max-width:800px) {
    .hero-left{
        padding-left: 50px;
    }
    .hero-left h2{
        font-size: 16px;
    }
    .hero-left p{
        font-size: 40px;
    }
    .hero-hand-icon img{
        width: 50px;
    }
    .hero-latest-btn{
       
        width: 180px;
        height: 40px;
        font-size: 14px;
        
    }
    .hero-right img{
        width:400px;
         
    }
    
}
@media (max-width:500px){
    .hero{
        flex-direction: column;
        height: 80vh;
    }
    .hero-left{
        position: absolute;
        left: 10%;
        top: 20%;
        height: 60vh;
    }
    .hero-left h2{
        font-size: 18px;

    }
    .hero-left p{
        font-size: 50px;
         
    }
    .hero-right{
        display: none;
    }
    .hero-hand-icon img{
        width: 55px;
    }
    .hero-latest-btn{
       
        width: 160px;
        height: 35px;
        font-size: 14px;
        
    }
}
